body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card {
  width: 126px;
  height: 176px;
  border-radius: 8px;
  border: 1px solid #000;
  padding: 10px;
  background-color: #f0f0f0;
  /* Add any other styles specific to the Card component */
}

.card.dragging {
  opacity: 0.5;
  /* Add any additional styles for the dragging state */
}
.card.card-back {
  background-image: repeating-linear-gradient(
    45deg,
    transparent,
    transparent 3px,
    #000 3px,
    #000 4px
  );
  /* Add any other styles for the card back design */
}

.zone {
  min-height: 176px;

  /* Add other styling properties */
}

.center-text {
  text-align: center;
}

.hand-zone {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px; /* Adjust the gap between cards as needed */
  min-height: 220px; /* Set a minimum height for the drop zone */
  min-width: 830px;
  border: 2px dashed #ccc; /* Add a dashed border for the drop zone */
}

.play-zone {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px; /* Adjust the gap between cards as needed */
  min-height: 550px; /* Set a minimum height for the drop zone */
  min-width: 1200px; /* Set a minimum height for the drop zone */
  border: 2px dashed #ccc; /* Add a dashed border for the drop zone */
}

.zones-row {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px; /* Adjust the gap between cards as needed */
}

.zones {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.button-container {
  margin-top: 5px;
}
.single-card-zone {
  width: 170px;
  height: 220px;
  border: 2px dashed #ccc;
  display: flex; /* Use flexbox for centering */
  justify-content: center; /* Center content horizontally */
  align-items: center; /* Center content vertically */
}

.card-list {
  height: 100%;
}

.footer {
  background-color: #f5f5f5;
  color: #777;
  padding: 20px;
  text-align: center;
  font-size: 14px;
}

.footer a {
  color: #777;
  text-decoration: none;
}

.footer a:hover {
  text-decoration: underline;
}

